/* Common css for whole website starts */

:root {
  --color1: #002;
  --color2: #0af;
  --color3: #222;
}

h2{
  color: var(--color1);
  margin-top: 2rem;
  padding-bottom: .5rem;
  margin-bottom: 1rem;
  border-bottom: 3px solid var(--color2);
  display: inline-block;
}

.jdflk:before{
  animation: rotate 80s infinite linear;
    -webkit-animation: rotate 80s infinite linear;
    background: linear-gradient(to right, rgba(255,120,16,1) 0%,rgba(255,21,106,1) 25%,rgba(255,94,44,1) 50%,rgba(187,13,76,1) 75%,rgba(69,204,137,1) 100%);
    
}



/* Common css for whole website ends */


@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

html {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;

}


::-webkit-scrollbar {
  background-color: var(--color1);
  width: 0.5rem;
}
::-webkit-scrollbar-button {
  display: none;
}
::-webkit-scrollbar-thumb {
  background-color: var(--color2);
  border-radius: 5px;
}
::-webkit-scrollbar-track {
  background-color: var(--color1);
}

*::selection {
  background-color: var(--color1);
  color: var(--color2);
}

