.navbar {
  height: 50px;
  width: 50px;
  background-color: var(--color2);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: fixed;
  top: 0;
  right: 0;
  margin: 10px;
  z-index: 99;
}

.menu-bars {
  font-size: 2rem;
  background: none;
  color: white;
}

.menu-bars-close{
  font-size: 2rem;
  margin-left: 190px;
}

.nav-menu-wrap{
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  display: flex;
  flex-direction: column;
  background-color: var(--color1);
  z-index: 89;
  min-height: 100vh;
  
}

.nav-menu {
  width: 275px;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
}

.nav-menu-wrap.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0 8px 16px;
  list-style: none;
  height: 60px;
}

svg {
  font-size: 24px;
}

.nav-text a {
  text-decoration: none;
  color: whitesmoke;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  color: var(--color2);
}


.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: var(--secondary-color);
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.footer {
  width: 100%;
  background-color: var(--color1);
  color: whitesmoke;
  padding: 0.5em;
  min-height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon {
  font-size: 2rem;
}

.quick-links {
  display: flex;
  flex-wrap: wrap;
}

.quick-links a {
  color: whitesmoke;
}

.quick-links a:hover {
  color: white;
}

.profileWrap{
  flex-direction: column;
}

.profileWrap h2{
  color: white;
}

.dp{
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin: 10px;
  border: 8px solid rgba(46, 39, 73, 0.4);
}

.nav-h2{
  border: none;
  margin: .5rem;
}

.social-media ul{
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 1rem;
  padding: 1rem;
  cursor: pointer;
}

.social-media ul li{
  background-color: rgba(46, 39, 73, 0.4);
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.social-media ul li:hover{
  background-color: var(--color2);
}

.social-media ul li, .center{
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width:769px) {
  .nav-menu-wrap {
      left: 0;
  }
}