.home{
    padding: 10px;
}

.hero{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    padding-bottom: 0;
    margin-bottom: 0;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
}

.hero-home{
    background-image: url(/public/images/undraw_into_the_night_vumi.svg);
    background-size: 70vw;
    background-color: #cff;
    background-position: bottom;
}


.big-heading{
    color: white;
    text-align: center;
    font-size: 3rem;
}

.about{
    min-height: 100vh;
    background-color: white;
    padding: 10px;
    padding-bottom: 0;
    margin: 0;
}

b, .about ul > li{
    line-height: 3;
}

.about .me{
    margin: 1rem;
    border: 8px solid #002;
}

.typewriter{
    font-size: 1.5rem;
    padding: 0 .5rem;
    border-radius: 10px;
    color: white;
    margin: 0;
    display: flex;
}

.typewriter-home{
    color: var(--secondary-color);
    font-weight: 900;
}

.typewriter > {
    padding: 0;
    margin: 0;
}

.home ul{
    display: inline-block;
}

.home ul li{
    color: #002;
}

@media screen and (min-width:769px) {
    .home, .youtube, .projects, .about, .hero{
        padding-left: 285px;
    }
    .nav-menu-wrap.active {
        left: 0;
    }
    .navbar{
        display: none;
    }
    .about-wrap{
        display: flex;
        flex-direction: row;
        gap: 2rem;
        align-items: center;
    }
}